}


.logo {
    height: clamp(1em,2vw + 0.5em,2em);
}

.avatar {
    height: clamp(1em, 2vw + 0.5em, 2em);
    /* padding-right: 2rem; */
}

/* When position is fixed, the navbar div is overflowing and the avatar on the right is being hidden. */

.navbar {
    position: fixed;
    width: 100%;
    padding: clamp(.1em, 2vw + 0.3em, 1em);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* background-color:rgb(20, 20, 20); */
    
}


